export default {
	data() {
		return {
			imgUrl: '/static/wenh.jpg',
			src: ''
		}
	},
	methods: {
		back () {
			uni.navigateBack({
				delta: 1
			})
		},
		startSoterAuthenticationFaceID() {
			uni.startSoterAuthentication({
				requestAuthModes: ['facial'],
				challenge: '123456',
				authContent: '请用FaceID解锁',
				success(res) {
					console.log(res);
				},
				fail(err) {
					console.log(err);
				},
				complete(res) {
					console.log(res);
				}
			})
		}
	}
}
